.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.modal-content {
  background-color: white;
  border-radius: 0.75rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  max-width: 800px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid #e5e7eb;
}
.modal-header .modal-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #111827;
  margin: 0;
}
.modal-header .close-btn {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 0.375rem;
  transition: all 0.2s;
}
.modal-header .close-btn:hover {
  background-color: #f3f4f6;
  color: #374151;
}

.edit-form {
  padding: 1.5rem;
}
.edit-form .form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 2rem;
}
.edit-form .form-section .section-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 1rem 0;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #e5e7eb;
}
.edit-form .form-group {
  margin-bottom: 1rem;
}
.edit-form .form-group .form-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.5rem;
}
.edit-form .form-group .form-input,
.edit-form .form-group .form-textarea,
.edit-form .form-group .form-select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: white;
  transition: all 0.2s;
}
.edit-form .form-group .form-input:focus,
.edit-form .form-group .form-textarea:focus,
.edit-form .form-group .form-select:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.edit-form .form-group .form-input::placeholder,
.edit-form .form-group .form-textarea::placeholder,
.edit-form .form-group .form-select::placeholder {
  color: #9ca3af;
}
.edit-form .form-group .form-textarea {
  resize: vertical;
  min-height: 80px;
}
.edit-form .form-group .form-select {
  cursor: pointer;
  appearance: none;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='m6 8 4 4 4-4'/%3e%3c/svg%3e");
  background-position: right 0.75rem center;
  background-repeat: no-repeat;
  background-size: 1.5em 1.5em;
  padding-right: 2.5rem;
}
.edit-form .form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding-top: 1.5rem;
  border-top: 1px solid #e5e7eb;
}
.edit-form .form-actions .btn {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;
  cursor: pointer;
  border: none;
  font-size: 0.875rem;
}
.edit-form .form-actions .btn.btn-secondary {
  background-color: white;
  color: #374151;
  border: 1px solid #d1d5db;
}
.edit-form .form-actions .btn.btn-secondary:hover {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.edit-form .form-actions .btn.btn-primary {
  background-color: #3b82f6;
  color: white;
}
.edit-form .form-actions .btn.btn-primary:hover {
  background-color: #2563eb;
}
.edit-form .form-actions .btn.btn-primary:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .modal-content {
    margin: 1rem;
    max-height: calc(100vh - 2rem);
  }
  .edit-form {
    padding: 1rem;
  }
  .edit-form .form-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  .edit-form .form-actions {
    flex-direction: column;
  }
  .edit-form .form-actions .btn {
    width: 100%;
  }
}
@media (max-width: 480px) {
  .modal-header {
    padding: 1rem;
  }
  .modal-header .modal-title {
    font-size: 1.25rem;
  }
  .edit-form {
    padding: 1rem;
  }
}
